import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { User } from 'src/model/user';


@Component({
  selector: 'app-constat',
  templateUrl: './constat.page.html',
  styleUrls: ['./constat.page.scss'],
})
export class ConstatPage implements OnInit {

  user: User;
  inputValue: string = "";
  desc: string="";
  currentDate = new Date();
  Constat: any;
  allConstats: any = [{ title: 'constat 1', date: this.currentDate, user: 'admin' }, { title: 'constat 2', date: this.currentDate, user: 'admin' }]
  isConstatActivated: boolean = true;
  isHistoricalActivated: boolean = false;
  historicalSuggesions: any = [];
  showConstatMessage: boolean;
  showConstatList: boolean;
  storageConstats: any = [];

  public pageInfo = {title:'Détection d\'', subTitle:'anomalies'};
  constructor(public alertController: AlertController, public router: Router,) { }

  async constatAlert(message) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      message: message,
      buttons: [
        {
          text: 'OK',
          handler: (alertData) => {
          }
        }
      ]
    });
    (<HTMLStyleElement>document.querySelector(".alert-message")).style.fontSize = "17px";
    (<HTMLStyleElement>document.querySelector(".alert-message")).style.fontWeight = "normal";
    await alert.present();
  }

  refreshPage() {
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }

  getConstat() {
    if (this.inputValue != "") {
      this.Constat = {
        title: this.inputValue,
        date: this.currentDate,
        user: this.user.firstname
      };
      this.allConstats.push(this.Constat);
      localStorage.setItem('constat', JSON.stringify(this.allConstats));
      this.constatAlert('Votre Constat a bien été prise en compte');
      // this.refreshPage();
       this.inputValue = ""
       this.desc=""
    } else {
      this.constatAlert('Veuillez ajouter une idée');
    }
  }

  showConstatCard() {
    this.isConstatActivated = true;
    this.isHistoricalActivated = false;
  }

  showHistoricalCard() {
    this.isConstatActivated = false;
    this.isHistoricalActivated = true;
    if (JSON.parse(localStorage.getItem('constat'))){
      this.historicalSuggesions= JSON.parse(localStorage.getItem('constat'))
    }
  }

  ngOnInit() {

    if (!JSON.parse(localStorage.getItem('constat'))) {
      localStorage.setItem('constat', JSON.stringify(this.allConstats))
    }
    this.allConstats = JSON.parse(localStorage.getItem('constat'))
    if (!this.allConstats || this.allConstats.length == 0) {
      this.showConstatMessage = true;
      this.showConstatList = false;
      this.allConstats = [];
    } else {
      this.showConstatMessage = false;
      this.showConstatList = true;
    }
    this.user = <User>{
      id: '1',
      firstname: 'moi',
      role_description: 'admin',
    }
    if (JSON.parse(localStorage.getItem('constat'))) {
      if (!this.allConstats || this.allConstats.length == 0) {

        console.log('Constat NUL')
      } else {
        this.allConstats.map(oneConstat => {
          this.historicalSuggesions.push({
            title: oneConstat.title,
            date: oneConstat.date,
            user: oneConstat.user
          })
        })
      }
    }
  }





}
